import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PaymentWrapper from 'components/payment/PaymentWrapper';
import PaymentList from 'components/payment/PaymentList';
import CartTable from 'components/cart/CartTable';
import AuthContent from 'components/auth/AuthContent';
import InputWithLabel from 'components/auth/InputWithLabel';
import InputWithLabelPhone from 'components/auth/InputWithLabelPhone';
import InputPhoneWrapper from 'components/auth/InputPhoneWrapper';
import { withRouter } from 'react-router-dom';
import { isEmail, isLength, isAlphanumeric } from 'validator';
import * as cartActions from 'store/modules/cart';
import * as paymentActions from 'store/modules/payment';
import Button from 'components/common/Button';
import storage from 'lib/storage';

if (typeof window === 'undefined') {
    global.window = {}
}
const $ = window.$;

var IMP = window.IMP;
const isBrowser = process.env.APP_ENV === 'browser';
if (isBrowser) {
    IMP.init('imp55957545');
}

const daum = window.daum;
class PaymentContainer extends Component {

    getCartList = async () => {
        const { CartActions } = this.props;
        const config = {
            headers: { Pragma: 'no-cache' }
        };

        try {
            await CartActions.getCartList(config);
        } catch (e) {
            console.log(e);
        }
    }

    getAccessToken = async () => {
        let merchant_uid = Math
            .random()
            .toString(36)
            .substring(7);
        const { PaymentActions, totalPrice } = this.props;
        const config = {
            headers: { Pragma: 'no-cache' }
        };
        try {
            await PaymentActions.getAccessToken(config);
            const config = {
                headers: { Pragma: 'no-cache' }
            };
            if (!this.props.iamportError) {
                await PaymentActions.getMerchantUid({ merchant_uid, accessToken: this.props.accessToken, amount: totalPrice }, config);

                if (this.props.iamportAmount === totalPrice) {
                    const {
                        totalPrice,
                        userEmail,
                        userName,
                        userPhonePost,
                        userPhoneCenter,
                        userPhoneRear,
                        userAddress,
                        userDetailAddress,
                        userPostCode,
                        CartActions
                    } = this.props;

                    let amount = parseInt(totalPrice, 10) + 3000;
                    let buyer_email = userEmail;
                    let buyer_name = userName;
                    let buyer_tel = userPhonePost + '-' + userPhoneCenter + '-' + userPhoneRear;
                    let buyer_address = userAddress + ' / ' + userDetailAddress;
                    let buyer_postcode = userPostCode;
                    let ordered_list = "";
                    let ordered_number = "";
                    let item_number = "";
                    let buyer_id = storage.get('loggedInfo').userID;
                    let splitted_ordered_list = "";
                    if (this.props.cartList.length > 1) {
                        splitted_ordered_list += `${this.props.cartList[0].title}-${this.props.cartList[0].option} ${this.props.cartList[0].amount}  ${this.props.cartList.length - 1}`;
                    } else {
                        splitted_ordered_list += `${this.props.cartList[0].title}-${this.props.cartList[0].option} ${this.props.cartList[0].amount}`;
                    }
                    this.props.cartList.map(
                        (cart, i) => {
                            ordered_list += cart.title + "-" + cart.option + ",";
                            ordered_number += cart.amount + ",";
                            item_number += cart.id + ",";
                        }
                    );

                    IMP.request_pay({
                        pg: 'html5_inicis',
                        pay_method: 'card',
                        merchant_uid: 'merchant_' + new Date().getTime(),
                        name: splitted_ordered_list,
                        amount: amount,
                        buyer_email: buyer_email,
                        buyer_name: buyer_name,
                        buyer_tel: buyer_tel,
                        buyer_addr: buyer_address,
                        buyer_postcode: buyer_postcode
                    }, function (rsp) {
                        if (rsp.success) {
                            $
                                .ajax({
                                    url: "/api/payment/complete",
                                    type: 'POST',
                                    dataType: 'json',
                                    data: {
                                        imp_uid: rsp.imp_uid,
                                        merchant_uid: rsp.merchant_uid,
                                        paid_amount: rsp.paid_amount,
                                        apply_num: rsp.apply_num,
                                        buyer_email: buyer_email,
                                        buyer_name: buyer_name,
                                        buyer_tel: buyer_tel,
                                        buyer_addr: buyer_address,
                                        buyer_postcode: buyer_postcode,
                                        ordered_list: ordered_list,
                                        ordered_number: ordered_number,
                                        buyer_id: buyer_id,
                                        item_number: item_number
                                    }
                                })
                                .done(function (data) {
                                    // console.log(data);
                                    var msg = ' ! .';
                                    // msg += 'ID : ' + rsp.imp_uid; msg += ' ID : ' + rsp.merchant_uid; msg
                                    // += '  : ' + rsp.paid_amount; msg += '  : ' + rsp.apply_num;
                                    alert(msg);
                                    const config = {
                                        headers: { Pragma: 'no-cache' }
                                    };
                                    CartActions.removeCart(config);
                                    document.location.href = "/";
                                });
                        } else {
                            var msg = ' .';
                            msg += ' : ' + rsp.error_msg;
                            alert(msg);
                        }
                    });
                }
            }

        } catch (e) {
            console.log(e);
        }
    }


    getUserInfo = async () => {
        const { PaymentActions } = this.props;
        let loggedInfo = storage.get('loggedInfo');

        try {
            await PaymentActions.getUserInfo({id: loggedInfo.id});
        } catch (e) {
            console.log(e);
        }
    }

    componentDidMount() {
        if (localStorage.memberLogged !== "true") {
            const { history } = this.props;
            alert("   .  .");
            history.push('/login');
            return;
        }
        // this.props.PaymentActions.initialize();
        this.getUserInfo();
        this.getCartList();



    }

    componentDidUpdate(prevProps, prevState) {
        const { history } = this.props;
        if (prevProps.cartList !== this.props.cartList) {
            if (this.props.cartList.length === 0) {
                alert("  .");
                history.push('/');
            }



        }
    }
    handleChange = (e) => {
        const { PaymentActions } = this.props;
        const { name, value } = e.target;
        PaymentActions.changeInput({ name, value });
    }

    handlePay = () => {
        const { validate } = this;
        const { userEmail, userName, userPhonePost, userPhoneCenter, userPhoneRear, userDetailAddress, userAddress, userPostCode } = this.props;
        if (!validate['email'](userEmail)
            || !validate['username'](userName)
            || !validate['phonePost'](userPhonePost)
            || !validate['phoneCenter'](userPhoneCenter)
            || !validate['phoneRear'](userPhoneRear)
            || !validate['detailAddress'](userDetailAddress)) {
            //    
            return;
        }
        if (!userAddress) {
            alert(" .");
            return;
        }
        if (!userPostCode) {
            alert(" .");
            return;
        }

        this.getAccessToken();

    }

    validate = {
        email: (value) => {
            if (!isEmail(value)) {
                alert('   .');
                return false;
            }
            return true;
        },
        username: (value) => {
            if (!isLength(value, { min: 2, max: 15 })) {
                alert('   .');
                return false;
            }
            return true;
        },
        phonePost: (value) => {
            if (!isLength(value, { min: 2, max: 3 })) {
                alert('   .');
                return false;
            }
            return true;
        },
        phoneCenter: (value) => {
            if (!isLength(value, { min: 3, max: 4 })) {
                alert('   .');
                return false;
            }
            return true;
        },
        phoneRear: (value) => {
            if (!isLength(value, { min: 3, max: 4 })) {
                alert('   .');
                return false;
            }
            return true;
        },
        detailAddress: (value) => {
            if (!isLength(value, { min: 1, max: 200 })) {
                alert('   .');
                return false;
            }
            return true;
        }
    }


    handleAddressMapOpen = () => {

        const { PaymentActions } = this.props;
        new daum.Postcode({
            oncomplete: function (data) {
                //        .       .    
                //  ('') ,    .
                var fullAddr = ''; //   
                var extraAddr = ''; //   

                //         .
                if (data.userSelectedType === 'R') { //     
                    fullAddr = data.roadAddress;

                } else { //     (J)
                    fullAddr = data.jibunAddress;
                }

                //      .
                if (data.userSelectedType === 'R') {
                    //   .
                    if (data.bname !== '') {
                        extraAddr += data.bname;
                    }
                    //    .
                    if (data.buildingName !== '') {
                        extraAddr += (extraAddr !== ''
                            ? ', ' + data.buildingName
                            : data.buildingName);
                    }
                    //         .
                    fullAddr += (extraAddr !== ''
                        ? ' (' + extraAddr + ')'
                        : '');
                }

                //      .
                $('input[id=buyer_postcode]').val(data.zonecode);
                $('input[id=buyer_addr]').val(fullAddr);
                PaymentActions.changeInput({ name: 'userPostCode', value: data.zonecode });
                PaymentActions.changeInput({ name: 'userAddress', value: fullAddr });

                //    .
                $('input[id=detail_addr]').focus();
            }
        }).open();
    }
    render() {
        const { handleAddressMapOpen, handleChange, handlePay } = this;
        const {
            cartList,
            totalPrice,
            userName,
            userEmail,
            userPhonePost,
            userPhoneCenter,
            userPhoneRear,
            userAddress,
            userPostCode,
            userDetailAddress,
            memberLogged
        } = this.props;



        return (

            <PaymentWrapper>
                <PaymentList title=" ">
                    <CartTable ver="payment" cartList={cartList} totalPrice={totalPrice} />
                    <AuthContent onButtonClick={handlePay} buttonText="">
                        <InputWithLabel
                            value={userName}
                            onChange={handleChange}
                            label=" "
                            name="userName"
                            placeholder=" " />
                        <InputPhoneWrapper label=" ">
                            <InputWithLabelPhone
                                value={userPhonePost}
                                onChange={handleChange}
                                name="userPhonePost"
                                placeholder=" " />
                            <InputWithLabelPhone
                                value={userPhoneCenter}
                                onChange={handleChange}
                                name="userPhoneCenter"
                                placeholder=" " />
                            <InputWithLabelPhone
                                value={userPhoneRear}
                                onChange={handleChange}
                                name="userPhoneRear"
                                placeholder=" " />
                        </InputPhoneWrapper>
                        <InputWithLabel
                            value={userEmail}
                            onChange={handleChange}
                            label=""
                            name="userEmail"
                            placeholder="" />
                        <InputWithLabel
                            id="buyer_addr"
                            onClick={handleAddressMapOpen}
                            value={userAddress}
                            onChange={handleChange}
                            label=" "
                            name="userAddress"
                            placeholder="  ." />
                        <InputWithLabel
                            id="buyer_postcode"
                            disabled="true"
                            label=""
                            value={userPostCode}
                            onChange={handleChange}
                            name="userPostCode"
                            placeholder="" />
                        <InputWithLabel
                            id="detail_addr"
                            label=" "
                            name="userDetailAddress"
                            value={userDetailAddress}
                            onChange={handleChange}
                            placeholder="  " />
                    </AuthContent>
                </PaymentList>
            </PaymentWrapper>
        );
    }
}

export default connect((state) => ({
    cartList: state
        .cart
        .get('cartList'),
    totalPrice: state
        .cart
        .get('totalPrice'),
    userName: state
        .payment
        .get('userName'),
    userPhonePost: state
        .payment
        .get('userPhonePost'),
    userPhoneCenter: state
        .payment
        .get('userPhoneCenter'),
    userPhoneRear: state
        .payment
        .get('userPhoneRear'),
    userAddress: state
        .payment
        .get('userAddress'),
    userPostCode: state
        .payment
        .get('userPostCode'),
    userDetailAddress: state
        .payment
        .get('userDetailAddress'),
    userEmail: state
        .payment
        .get('userEmail'),
    memberLogged: state
        .base
        .get('memberLogged'),
    iamportError: state
        .payment
        .get('iamportError'),
    accessToken: state
        .payment
        .get('accessToken'),
    merchantUid: state
        .payment
        .get('merchantUid'),
    iamportAmount: state
        .payment
        .get('iamportAmount'),
    userInfo: state.payment.get('userInfo')

}), (dispatch) => ({
    CartActions: bindActionCreators(cartActions, dispatch),
    PaymentActions: bindActionCreators(paymentActions, dispatch)
}))(withRouter(PaymentContainer));


